 .loaderWrapper {
     height: 100vh;
     width: 100%;
     display: flex;
     justify-content: center;
     align-items: center;
     margin: auto;
 }


 .news-container {

     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;

     padding-top: 5%;
     padding-bottom: 5%;
 }

 .insight-cardWrapper {
     width: 100%;
     height: auto;
     display: flex;
     justify-content: space-evenly;
     align-items: center;
     flex-direction: column;
     padding: 10px;
     background-color: yellow;
 }

 .insight-card:nth-child(odd) {
     width: 80%;
     height: auto;
     display: flex;
     align-items: flex-start;
     justify-content: flex-start;
     flex-direction: column;
     gap: 20px;
     border-bottom: 1px solid lightgreen;
     border-left: 1px solid rgb(255, 209, 124);
     padding-bottom: 30px;
     padding-left: 20px;
     padding-top: 20px;
 }

 .insight-card:nth-child(even) {
     width: 80%;
     height: auto;
     display: flex;
     align-items: flex-start;
     justify-content: flex-start;
     flex-direction: column;
     gap: 20px;
     border-right: 1px solid rgb(112, 198, 255);
     border-bottom: 1px solid rgb(228, 171, 255);
     padding-bottom: 30px;
     padding-right: 20px;
     padding-top: 20px;
 }

 .insight-card a {
     position: relative;
     display: flex;
     height: auto;
     width: auto;
     color: rgb(64, 172, 214);
     text-decoration: none;
     transition: 500ms all ease;
 }

 .insight-card a:hover {
     color: white;
 }

 .insight-card a h6 {
     padding: 2px 10px;
     z-index: 1;
     margin: auto;
 }

 .insight-card a::before {
     content: "";
     background-color: black;
     position: absolute;
     height: 100%;
     width: 5%;
     display: flex;
     transition: 500ms all ease;
 }

 .insight-card a:hover::before {
     width: 100%;
 }

 .insight-card .insight-newsAuthor {
     display: flex;
     width: 100%;
     justify-content: space-between;
     align-items: center;
 }

 .insight-card .insight-newsAuthor p {
     font-size: 12px;
 }

 .in-cardImgWrapper {
     margin: auto;
     height: auto;
     display: flex;
     position: relative;
     width: 100%;
 }

 .in-cardImgWrapper img {
     height: auto;
     width: 100%;
     object-fit: contain;
 }

 @media screen and (max-width: 900px) {
     .Insight-sectionWrapper {
         justify-content: space-evenly;
         align-items: center;
         display: flex;
         flex-direction: column;
         padding: 5%;
         padding-top: 15%;
         font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
     }

     .insight-card:nth-child(odd),
     .insight-card:nth-child(even) {
         width: 100%;
     }
 }