.researchWrapper {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    overflow: hidden;
    flex-direction: column;
    gap: 20px;
}

.r-section-1,
.r-section-2 {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 20px;
    justify-content: flex-start;
    align-items: flex-start;
    position: relative;
}

.r-cards {
    display: flex;
    flex: 1;
    height: 100%;
    flex-direction: row;
    gap: 20px;
    padding: 20px 10%;
}

.cards {
    width: 100%;
    color: white;
    transition: 500ms all ease;
    display: flex;
    flex-direction: column;

    border-radius: 20px;

    padding: 20px;
}

.cards:hover {
    transition: all 10ms ease-in-out;
    box-shadow: 0px 3px 5px 2px rgba(50, 205, 50, 0.247)
}

.cards h3 {
    margin-bottom: 20px;
    color: #898989;
}

.cards p {
    color: #898989;
}

.r-writeup {
    display: flex;
    flex-direction: row-reverse;
    width: 80%;
    height: auto;
    justify-content: center;
    align-items: flex-start;
    gap: 10px;
    margin: auto;
    border-radius: 10px;
    overflow: hidden;
    color: white;
    background: linear-gradient(to bottom right, green, skyblue);
    padding: 20px 5%;
    position: relative;
}

.r-writeupBckImageWrapper {
    position: absolute;
    display: flex;
    margin: auto;
    width: 100%;
    height: 100%;
    overflow: hidden;

}

.r-writeupBckImageWrapper img {
    width: 100%;
    height: 100%;
    display: flex;
    margin: auto;
    mix-blend-mode: multiply;
    opacity: .5;
}

.r-writeUpWrapper {
    flex-direction: column;
    padding: 20px;
    display: flex;
    height: 100%;
    width: 100%;
    z-index: 1;
}

.r-writeUpWrapper h1 {
    margin-bottom: 30px;
    color: #ffffff;
    letter-spacing: 2px;
}

.r-writeUpWrapper p span {
    font-weight: bold;
    background: linear-gradient(to right, rgb(167, 218, 27), rgb(144, 238, 149));
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
}

.r-writeupImageWrapper {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    border-radius: 0px;
    overflow: hidden;
}

.r-writeupImageWrapper img {
    object-fit: cover;
    display: flex;
    margin: auto;
    height: 100%;
    width: 100%;
    z-index: 1;
}

@media screen and (max-width:900px) {
    .researchWrapper {
        gap: 20px;
        width: 100%;
        height: auto;
        display: flex;
        margin: auto;
        overflow: hidden;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    }

    .r-section-1,
    .r-section-2 {
        display: flex;
        flex-direction: column;
        width: 100%;
        gap: 20px;
        justify-content: flex-start;
        align-items: flex-start;
        position: relative;
    }

    .r-cards {
        display: flex;
        flex-direction: column;
        flex: 1;
        height: 100%;
        flex-direction: column;
        gap: 20px;
        padding: 0px 5%;
    }

    .cards {
        padding: 15px;
        width: 100%;
        color: white;
        transition: 500ms all ease;
        border-width: 2px;
    }

    .r-writeup {
        display: flex;
        flex-direction: column;
        width: 90%;
        height: auto;
        justify-content: center;
        align-items: flex-start;
        gap: 5px;
        margin: auto;
        border-radius: 10px;
        overflow: hidden;
        color: white;
        padding: 15px 2%;
        position: relative;
    }

    .r-writeupImageWrapper {
        width: 100%;
        height: 200px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: auto;
        border-radius: 20px;
        overflow: hidden;
    }

    .r-writeupImageWrapper img {
        object-fit: cover;
        display: flex;
        margin: auto;
        height: 100%;
        width: 100%;
        z-index: 1;
    }
}